import { getImage } from './js/pixabay-api.js';
import { markup } from './js/render-functions.js';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import { refs, showBtn, showLoader, hiddenBtn, hideLoader } from './js/refs.js';

const lightbox = new SimpleLightbox('.gallery li', {
  captionsData: 'alt',
  captionDelay: 250,
});

let page = 1;
let searchQuery = null;

refs.form.addEventListener('submit', async e => {
  e.preventDefault();
  refs.gallery.innerHTML = '';
  showLoader();
  hiddenBtn();
  page = 1;
  searchQuery = e.currentTarget.elements['search'].value.trim();
  const value = e.target.elements.search.value.trim();
  if (value === '') {
    return iziToast.warning({
      title: 'warning',
      message: 'Enter a word for the query, please.',
      position: 'topRight',
      displayMode: 'once',
    });
  }

  try {
    const res = await getImage(searchQuery, page);
    refs.gallery.innerHTML = markup(res.hits);
    lightbox.refresh();
    if (res.totalHits > 15) {
      showBtn();
    }
  } catch (error) {
    console.log(error);
  } finally {
    e.target.reset();
    hideLoader();
  }
});

refs.loadMoreBtn.addEventListener('click', async () => {
  page++;
  showLoader();
  try {
    const res = await getImage(searchQuery, page);
    refs.gallery.insertAdjacentHTML('beforeend', markup(res.hits));
    lightbox.refresh();
    //================================scrol
    const { height: cardHeight } = document
      .querySelector('.gallery')
      .firstElementChild.getBoundingClientRect();

    window.scrollBy({
      top: cardHeight * 2,
      behavior: 'smooth',
    });
    //===============================/scrol
    const lastPage = Math.ceil(res.total / 15);
    if (page === lastPage) {
      hiddenBtn();
      iziToast.info({
        position: 'topRight',
        message: "We're sorry, but you've reached the end of search results.",
      });
    }
  } catch (error) {
    console.log(error);
  } finally {
    hideLoader();
  }
});
